	const innerAudioContext = uni.createInnerAudioContext()
	innerAudioContext.autoplay = true
	innerAudioContext.loop = true
	innerAudioContext.src = 'https://music.163.com/song/media/outer/url?id=28287132.mp3'
	
	import uniPopup from "@/components/uni-popup/uni-popup.vue"
    var dateUtils = require('../../common/util.js').dateUtils;
    export default {
		components: {
			uniPopup	
		},
        data() {
            return {
				showWelcome:true,//显示欢迎界面
				swiperHeight: '1600upx',//
				height:'',
				modalName: null,
				directionStr: '水平',
				horizontal: 'right',
				vertical: 'bottom',
				direction: 'vertical',
				pattern: {
					color: '#7A7E83',
					backgroundColor: '#fff',
					selectedColor: '#690000',
					buttonColor: '#690000'
				},
                banner: {},
                listData: [],
                last_id: "",
				reload: false,
				//搜索框相关
				defaultKeyword: "",
				keyword: "",
				oldKeywordList: [],
				hotKeywordList: [],
				keywordList: [],
				forbid: '',
                isShowKeywordList: false
            }
		},
		onLoad() {
			//自动获取这两个
		    this.getBanner();
		    this.getList();
		    this.init();
		},
		onReady() {
			var tempHeight = 800;
			var _me = this;
			uni.getSystemInfo({
				//获取手机屏幕高度信息，让swiper的高度和手机屏幕一样高                
				success: (res)=> {                   
					tempHeight = res.windowHeight;
					console.log("屏幕可用高度 " + tempHeight);
					_me.swiperHeight = tempHeight + 'px';
					console.log("滑屏最后高度 " + _me.swiperHeight);
					this.height = _me.swiperHeight;
					console.log("hello world")
					console.log(this.height);
					this.welcomeHeight = tempHeight-100;					
				}
			});
		},
		onBackPress() {
			if (this.$refs.fab.isShow) {
				this.$refs.fab.close()
				return true
			}
			return false
		},
        onReachBottom() {
            this.getList();
        },
        methods: {
			showContent: function(e) {
				this.showWelcome = false
				// console.log(this.showWelcome)
			},
			backIndex: function(e) {
				this.showWelcome = true
				// console.log(this.showWelcome)
			},
			
			//进入详情页面
            goDetail: function(e) {
                uni.navigateTo({
                    url: "../detail/detail?id=" + e.id
                })
            },
            //显示搜索历史
            showHistory(msg){
				this.isShowKeywordList = msg;
				// console.log("hello World");
			},
			//隐藏搜索历史
            hideHistory(msg){
				this.isShowKeywordList = msg;
				console.log("hello World");
			},
			init() {
				this.loadDefaultKeyword();
				this.loadOldKeyword();
				this.loadHotKeyword();
			},
			blur(){
				uni.hideKeyboard()
			},
			//加载默认搜索关键字
			loadDefaultKeyword() {
				//定义默认搜索关键字，可以自己实现ajax请求数据再赋值,用户未输入时，以水印方式显示在输入框，直接不输入内容搜索会搜索默认关键字
				this.defaultKeyword = "请输入搜索信息";
			},
			//加载历史搜索,自动读取本地Storage
			loadOldKeyword() {
				uni.getStorage({
					key: 'OldKeys',
					success: (res) => {
						var OldKeys = JSON.parse(res.data);
						this.oldKeywordList = OldKeys;
					}
				});
			},
			//加载热门搜索
			loadHotKeyword() {
				//定义热门搜索关键字，可以自己实现ajax请求数据再赋值
				this.hotKeywordList = ['自来水煮过数次真的不能再次饮用吗？', '在外吃饭用开水烫碗筷真的有用吗？', '只吃素菜不吃荤菜真的可以减少健康问题吗？','睡眠时间越长越好吗？','热鸭梨水能抗癌吗？','缺维生素B2会致癌吗？','雪梨银耳能清肺？'];
			}, 
			//监听输入
			inputChange(event) {
				//兼容引入组件时传入参数情况
				var keyword = event.detail?event.detail.value:event;
				if (!keyword) {
					this.keywordList = [];
					this.isShowKeywordList = false;
					return;
				}
			},
			//高亮关键字
			drawCorrelativeKeyword(keywords, keyword) {
				var len = keywords.length,
					keywordArr = [];
				for (var i = 0; i < len; i++) {
					var row = keywords[i];
					//定义高亮#9f9f9f
					var html = row[0].replace(keyword, "<span style='color: #9f9f9f;'>" + keyword + "</span>");
					html = '<div>' + html + '</div>';
					var tmpObj = {
						keyword: row[0],
						htmlStr: html
					};
					keywordArr.push(tmpObj)
				}
				return keywordArr;
			},
			//顶置关键字
			setkeyword(data) {
				this.keyword = data.keyword;
			},
			//清除历史搜索
			oldDelete() {
				uni.showModal({
					content: '确定清除历史搜索记录？',
					success: (res) => {
						if (res.confirm) {
							console.log('用户点击确定');
							this.oldKeywordList = [];
							uni.removeStorage({
								key: 'OldKeys'
							});
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			},
			//热门搜索开关
			hotToggle() {
				this.forbid = this.forbid ? '' : '_forbid';
			},
			//执行搜索
			doSearch(key) {
				key = key ? key : this.keyword ? this.keyword : this.defaultKeyword;
				// this.keyword = key;
				//清空上一次搜索结果
				this.listData = [];
				this.saveKeyword(key); //保存为历史 
				this.reload = true;
				this.last_id = "";
				this.keyword = key;
                // console.log(key+" a 为什么啊")
                this.getList();
                //隐藏下拉列表
                this.isShowKeywordList = false;
			},
			//清楚搜索
			clearSearch(msg){
				this.isShowKeywordList = msg;
			},
			//保存关键字到历史记录
			saveKeyword(keyword) {
				uni.getStorage({
					key: 'OldKeys',
					success: (res) => {
						console.log(res.data);
						var OldKeys = JSON.parse(res.data);
						var findIndex = OldKeys.indexOf(keyword);
						if (findIndex == -1) {
							OldKeys.unshift(keyword);
						} else {
							OldKeys.splice(findIndex, 1);
							OldKeys.unshift(keyword);
						}
						//最多10个纪录
						OldKeys.length > 10 && OldKeys.pop();
						uni.setStorage({
							key: 'OldKeys',
							data: JSON.stringify(OldKeys)
						});
						this.oldKeywordList = OldKeys; //更新历史搜索
					},
					fail: (e) => {
						var OldKeys = [keyword];
						uni.setStorage({
							key: 'OldKeys',
							data: JSON.stringify(OldKeys)
						});
						this.oldKeywordList = OldKeys; //更新历史搜索
					}
				});
			},		
            //清除输入
            clearInput:function(e){
                this.keyword = "";
                this.getList();
                this.$refs.popupEmpty.close();

			},
			exit:function(e){
				this.$refs.popupBottom.close();
			},
			switchBtn(hor, ver) {
				if (hor === 0) {
					this.direction = this.direction === 'horizontal' ? 'vertical' : 'horizontal'
					this.directionStr = this.direction === 'horizontal' ? '垂直' : '水平'
				} else {
					this.horizontal = hor
					this.vertical = ver
				}
				this.$forceUpdate()
			}
        },
    }
